* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-color: transparent;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

div {
    justify-content: center;
}

.App {
    font-family: sans-serif;
    text-align: center;
}

.siteName {
    font-size: 30px;
    text-decoration: none;
    color: white;
}

.siteName:visited {
    color: white;
}

.header {
    padding-top: 1.5%;
    justify-content: center;
    padding-right: 5%;
    padding-left: 5%;
    background-color: #193948;
    padding-bottom: 2%;
}

.navLinks a {
    color: white;
}

.navLinks a:hover {
    color: #fcdc73;
}

.footerLinks a:hover {
    color: #fcdc73;
}

ul {
    list-style-type: none;
    text-decoration: none;
}

li {
    float: right;
}

li a {
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.burger {
    font-size: 20px;
    display: none;
    cursor: pointer;
}

.btn {
    padding: 15%;
    color: #57d655;
    border-radius: 10%;
    border-width: 3px;
    border-color: #57d655;
}

.rectangle {
    height: 70px;
    background-color: white;
}

.img {
    height: 20px;
    padding-right: 1%;
    background-color: transparent;
}

.footer {
    display: flex;
    justify-content: flex-start;
    margin-top: 8%;
    padding-top: 2%;
    color: white;
}

.footer a {
    color: white;
}

.footerContainer {
    position: relative;
    left: 0%;
    padding-left: 5%;
    bottom: 5%;
    margin-right: 5%;
    background-color: #193948;
    width: 100%;
    color: white;
}

.footerRight {
    margin-left: 1%;
    padding-top: 1%;
    font-size: 12px;
    padding-bottom: 2%;
}

hr {
    margin-top: 4%;
    margin-bottom: 1%;
}


/*Card styles*/

p {
    line-height: 2rem;
}

.card-container {
    display: flex;
    background-color: #f6f6f6;
    justify-content: flex-start;
    width: 80%;
    margin-top: 15px;
    height: 20%;
    border-radius: 10px;
    margin-bottom: 1%;
}

.img-container {
    justify-content: flex-start;
    display: flex;
    width: 18%;
    text-align: center;
    border-radius: 10px 0px 0px 10px;
}

.task-container {
    width: 80%;
    padding-top: 2%;
    padding-left: 2%;
    background-color: #f6f6f6;
}

.time-container {
    display: flex;
    justify-content: inline;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2%;
    position: relative;
}

@media all and (max-width: 768px) {
    .header {
        display: inline-flex;
        width: 100%;
        padding-left: 5%;
        justify-content: space-around;
        flex-direction: row;
    }
    .header .burger {
        display: inline;
        padding-right: 0%;
        color: black;
    }
    nav .navLinks {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        height: 100vh;
        padding: 40px;
        width: 50%;
        transform: translateX(-500px);
        transition: 0.5s ease-in-out;
        background-color: #193948;
    }
    nav .navLinks li {
        padding: 25px;
        border-bottom: 1px solid black;
        background-color: transparent;
    }
    nav .navLinks li a {
        background-color: transparent;
    }
    nav .navLinks li:last-of-type {
        border-bottom: 0px solid black;
    }
    nav .navLinks a:hover {
        border-bottom: 0px solid black;
    }
}


/* The alert message box */

.alert {
    position: relative;
    padding: 1rem 1rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: 0.25rem;
}

.error {
    color: #842029;
    background-color: #f8d7da;
    border-color: #f5c2c7;
}

.success {
    color: #0f5132;
    background-color: #d1e7dd;
    border-color: #badbcc;
}

.warning {
    color: #664d03;
    background-color: #fff3cd;
    border-color: #ffecb5;
}

.primary {
    color: #084298;
    background-color: #cfe2ff;
    border-color: #b6d4fe;
}

.secondary {
    color: #41464b;
    background-color: #e2e3e5;
    border-color: #d3d6d8;
}

.info {
    color: #055160;
    background-color: #cff4fc;
    border-color: #b6effb;
}


/* The close button */

.closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
}


/* When moving the mouse over the close button */

.closebtn:hover {
    color: black;
}

.hide {
    display: none;
}